import React, { useEffect } from 'react'
import { useSelector } from 'react-redux'
import graveStone from './images/grave_stone.png'
import './Dead.css'
import deathSound from './sounds/death_sound.mp3'
import deathSoundZombie from './sounds/death_sound_zombie.wav'

function Dead() {
  const userRole = useSelector(state => state.inGame.role)
  useEffect(() => {
    new Audio(userRole === 'zombie' ? deathSoundZombie : deathSound).play()
  }, [])
  return (
    <div className="Dead">
      <h1>RIP</h1>
      <img src={graveStone}/>
    </div>
  )
}

export default Dead
